<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Edit Product
      </div>
    </v-card-title>
    <v-card  class="pa-2 ma-2">
      <v-text-field
        v-model="form.ProductName"
        label="Product Name"
        :error-messages="errors.ProductName"
        outlined
      ></v-text-field>
      <v-textarea
        v-model="form.ProductDescription"
        label="Product Description"
        :error-messages="errors.ProductDescription"
        outlined
      ></v-textarea>
      <v-text-field
        v-model="form.FinancePurpose"
        label="FinancePurpose"
        :error-messages="errors.FinancePurpose"
        outlined
      ></v-text-field>
      <v-img :src="form.ImagesPath" width="100px"></v-img>
      <v-file-input
        show-size
        outlined
        label="Edit Image"
        accept="image/*"
        @change="createBase64Image"
      ></v-file-input>
      <v-select
        v-model="form.isactive"
        :items="items"
        item-text="text"
        item-value="value"
        label="Status"
        outlined
      ></v-select>

      <v-btn
        color="primary"
        class="mr-4"
        :loading="data.loading"
        @click="update(form)"
      >
        S A V E
      </v-btn>

      <v-btn
        color="error"
        class="mr-4"
        to='Product'
      >
        C A N C E L
      </v-btn>
    </v-card>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'

export default {
  name: 'ProductEdit',
  data: () => ({
    errors: {},
    form: {
      ProductName: store.getters['product/getDataDetail'].ProductName,
      ProductDescription: store.getters['product/getDataDetail'].ProductDescription,
      FinancePurpose: store.getters['product/getDataDetail'].FinancePurpose,
      isactive: store.getters['product/getDataDetail'].isactive,
      ImagesPath: store.getters['product/getDataDetail'].ImagesPath
    },
    items: [
      {
        text: 'Active', value: '1'
      },
      {
        text: 'No Active', value: '0'
      }
    ]
  }),
  computed: {
    ...mapState('product', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('product', [
      'update'
    ]),
    createBase64Image: function (FileObject) {
      const reader = new FileReader()
      reader.onload = (event) => {
        this.form.ImagesPath = event.target.result
      }
      reader.readAsDataURL(FileObject)
    }
  }
}
</script>
